<template>

	<div class="container">
		<div class="upload-form">
			<div class="upload-item">
				<el-upload class="avatar-uploader" 
				ref="upload01"
				:http-request="uploadFile" 
				:action="this.$axios.defaults.baseURL+'/post_content'"
			    :show-file-list="false" 
				:auto-upload="true" 
				:multiple='false' 
				:before-upload="beforeContentUpload" 
				:on-change="handleContentAccept">
					<img v-if="post_content" :src="post_content" class="avatar">
					<i v-else class="el-icon-plus avatar-uploader-icon"></i>
				</el-upload>
			</div>

			<div class="upload-item">
				<el-upload class="avatar-uploader" 
				ref="upload02"
				:http-request="uploadFile" 
				:action="this.$axios.defaults.baseURL+'/post_style'" 
				:show-file-list="false"
				:auto-upload="true"  
				:multiple="false"
				:before-upload="beforeStyleUpload" 
				:on-change="handleStyleAccept">
					<img v-if="post_style" :src="post_style" class="avatar">
					<i v-else class="el-icon-plus avatar-uploader-icon"></i>
				</el-upload>
			</div>


		</div>
		<el-button type="danger" @click="cleanAll"></el-button>
		<el-button type="primary" @click="submitImg"></el-button>

		<div class="block">
			<el-image :src="src?src:this.$axios.defaults.baseURL+'/img_test'">
				<div slot="placeholder" class="image-slot">
					<span class="dot">...</span>
				</div>
			</el-image>
		</div>

	</div>


</template>

<script>
	// import imageConversion from 'image-conversion'
	import {compress, compressAccurately} from 'image-conversion';
	export default {

		data() {
			return {
				src: '',
				imageUrl: '',
				post_style: '',
				post_content: '',
				filelist: '',
				formdata: ''
			};
		},
		mounted() {
			this.formdata = new FormData();
		},
		created() {

		},
		methods: {
			compressAccurately,
			compress,

			cleanAll() {
				this.$router.go(0);
			},
			initData() {
				this.$axios.get("/img_test").then(res => {
					this.src = URL.createObjectURL(res)
					console.log(URL.createObjectURL(res))
				})
			},
			uploadFile(file) {
				this.formdata.append('file', file.file)
			},
			handleContentAccept(file, filelist) {
				this.post_content = URL.createObjectURL(file.raw);
			},
			handleStyleAccept(file, filelist) {
				this.post_style = URL.createObjectURL(file.raw);
			},
			submitImg() {
				this.$message.info("... (10)")

				this.$axios.post("/post_style", this.formdata).then(res => {
					this.src = "data:image/jpeg;base64," + res.data

					this.$message.success("")
				}).catch(err=>{
					this.$message.error("")
					
				})
			},
			beforeStyleUpload(file) {
				console.log(file)
				console.log(file.size)
				 new Promise((resolve, reject) => {
					let isLt2M = file.size / 1024 / 1024 < 0.2 // 4MB
					if (isLt2M) {
						resolve(file)
					}
					const old = file.size
					compressAccurately(file, 100).then(res => { // console.log(res)
						console.log(old, res.size)
						resolve(res)
					})
				})
			},

			beforeContentUpload(file) {
				console.log(file)
				console.log(file.size)
				 new Promise((resolve, reject) => {
					let isLt2M = file.size / 1024 / 1024 < 0.5 // 4MB
					if (isLt2M) {
						resolve(file)
					}
					const old = file.size // 400KB,400,
					compressAccurately(file, 500).then(res => { // console.log(res)
						console.log(old, res.size)
						resolve(res)
					})
				})
			},

		}
	}
</script>

<style>
	.container {
		margin: 20px 10% 10% 10%;
	}

	.upload-form {}

	.block {
		margin: 20px 20% 20px 20%;
	}

	.upload-item {
		margin: 20px 10% 20px 10%;
		display: inline-block;
	}

	.avatar-uploader .el-upload {
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}

	.avatar-uploader .el-upload:hover {
		border-color: #409EFF;
	}

	.avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 178px;
		height: 178px;
		line-height: 178px;
		text-align: center;
	}

	.avatar {
		width: 178px;
		height: 178px;
		display: block;
	}
</style>



