<template>
	<div>
		<el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect" background-color="#545c64"
		 text-color="#fff" active-text-color="#ffd04b">
			<el-menu-item index="1"></el-menu-item>
			<el-submenu index="2">
				<template slot="title"></template>
				<el-menu-item index="2-1"></el-menu-item>
				<el-menu-item index="2-2"></el-menu-item>
				<el-menu-item index="2-3"></el-menu-item>
				<el-menu-item index="2-4"></el-menu-item>
				<el-menu-item index="2-5"></el-menu-item>
				<!-- 	    <el-submenu index="2-4">
	      <template slot="title">4</template>
	      <el-menu-item index="2-4-1">1</el-menu-item>
	      <el-menu-item index="2-4-2">2</el-menu-item>
	      <el-menu-item index="2-4-3">3</el-menu-item>
	    </el-submenu> -->
			</el-submenu>
			<el-menu-item index="3" disabled></el-menu-item>
		</el-menu>

		<comp1></comp1>

	</div>

</template>




<script>
	import comp1 from './Index.vue'
	export default {
		
		

		components: {
			comp1
		},
		data() {
			return {
				activeIndex: '1',
				flags: [],

			};
		},
		mounted() {

			this.initData()

		},
		methods: {
			initData() {
				this.$axios.get("/test").then(res => {
					this.$message.success("" + res.data)
				})
			},
			handleSelect(key, keyPath) {
				console.log(key, keyPath);
				this.flags = [0, 0, 0, 0, 0]
				if(keyPath == '2-1'){
					this.flags[0] = 1
				}
				

			}

		}
	}
</script>

<style>
	.container {
		margin: 20px 10% 10% 10%;
	}

	.upload-form {}

	.block {
		margin: 20px 20% 20px 20%;
	}

	.upload-item {
		margin: 20px 10% 20px 10%;
		display: inline-block;
	}

	.avatar-uploader .el-upload {
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}

	.avatar-uploader .el-upload:hover {
		border-color: #409EFF;
	}

	.avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 178px;
		height: 178px;
		line-height: 178px;
		text-align: center;
	}

	.avatar {
		width: 178px;
		height: 178px;
		display: block;
	}
</style>



