import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import App from './App.vue'
import router from './router.js'
// import axios from 'axios'
import axios_init from './axios_config.js'
import "babel-polyfill"

const baseURL = 'http://localhost:8000'    //linux on 5002 - 5005 . 5004
// const baseURL = 'http://modderbug.cn:5004'    //linux on 5002 - 5005 . 5004


// Vue.prototype.$axios = axios
Vue.prototype.$axios = axios_init
Vue.prototype.$axios.defaults.baseURL = baseURL // `baseURL`  `url`  `url`  URL



Vue.use(ElementUI)

new Vue({
	router,
	el: '#app',
	render: h => h(App)
})



