<template>
	<p>about</p>

</template>

<script>
	
	export default {
		data() {
			return {
				imageUrl: ''
			};
		},
		mounted() {
			console.log("aaaaa")
			initData()
		},
		beforeCreate() {
			initData()
		},
		methods: {
			initData() {
				this.$axios.get("/").then(res => {
					console.log(res)
				})
			},

			handleAvatarSuccess(res, file) {
				this.imageUrl = URL.createObjectURL(file.raw);
			},
			beforeAvatarUpload(file) {
				const isJPG = file.type === 'image/jpeg';
				const isLt2M = file.size / 1024 / 1024 < 2;

				if (!isJPG) {
					this.$message.error(' JPG !');
				}
				if (!isLt2M) {
					this.$message.error(' 2MB!');
				}
				return isJPG && isLt2M;
			}
		}
	}
</script>

<style>
	.avatar-uploader .el-upload {
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}

	.avatar-uploader .el-upload:hover {
		border-color: #409EFF;
	}

	.avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 178px;
		height: 178px;
		line-height: 178px;
		text-align: center;
	}

	.avatar {
		width: 178px;
		height: 178px;
		display: block;
	}
</style>



