<template>
	<div class="login_form">


		
		{{res_text}}

	</div>
</template>

<script>
	import crypto from 'crypto'
	export default {
		data() {
			var validateUser = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''));
				}
			};
			var validatePass = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''));
				}
			};
			return {
				res_text:'',
				ruleForm: {
					username: 'admin',
					password: 'admin'
				},
				rules: {
					username: [{
						validator: validateUser,
						trigger: 'blur'
					}],
					password: [{
						validator: validatePass,
						trigger: 'blur'
					}]
				}
			};
		},
		methods: {
			hash_encrtpt(data){
				    var buf = new Buffer(data);
				    var str = buf.toString("binary");
					var salt = new Buffer("10");
					var salt_str = salt.toString("binary");
				    return crypto.createHash("md5").update(salt_str).update(str).digest("hex");
			}, 
			
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let form = {...this.ruleForm}

						form.password = this.hash_encrtpt(this.ruleForm.password)

						console.log(form)

						this.$axios.post("/api/login", form).then(res => {
							// console.log(res.data)
							window.localStorage.setItem("spider_web",res.data.token)
							// console.log(window.localStorage.get("spider_web"))
						})


					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			init_data() {
				this.$axios.get("/api/user/info").then(res => {
					console.log(res.data)
					this.res_text = res.data
				})
			}
		},
		created() {
			this.init_data()

		}
	}
</script>


<style>
	.container {
		margin: 20px 10% 10% 10%;
	}

	.login_form {

		position: absolute;

		top: 10%;
		left: 30%;

		width: 30%;
		height: 400px;

	}
</style>



