<template>

	<div class="login_form">
		<el-card class="box-card">
			<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
				<el-form-item label="" prop="pass">
					<el-input type="password" v-model="ruleForm.username" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="checkPass">
					<el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
				</el-form-item>

				<el-form-item>
					<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
					<el-button @click="resetForm('ruleForm')"></el-button>
				</el-form-item>
			</el-form>
		</el-card>
	</div>

</template>

<script>
	import crypto from 'crypto'
	export default {
		data() {
			var validateUser = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''));
				}
			};
			var validatePass = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''));
				}
			};
			return {
				ruleForm: {
					username: 'admin',
					password: 'admin'
				},
				rules: {
					username: [{
						validator: validateUser,
						trigger: 'blur'
					}],
					password: [{
						validator: validatePass,
						trigger: 'blur'
					}]
				}
			};
		},
		methods: {
			//  npm install crypto   import crypto from 'crypto'
			hash_encrtpt(data) {
				var buf = new Buffer(data);
				var str = buf.toString("binary");
				var salt = new Buffer("10");
				var salt_str = salt.toString("binary");
				return crypto.createHash("md5").update(salt_str).update(str).digest("hex");
			},
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let form = { ...this.ruleForm
						}
						// 
						form.password = this.hash_encrtpt(this.ruleForm.password)
						this.$axios.post("/api/login", form).then(res => {
							console.log(res.data)
							window.localStorage.setItem("spider_web", res.data.token)
							console.log(window.localStorage.getItem("spider_web"))
							// 
							this.$router.push('/userinfo')
						})

					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			resetForm(formName) {
				this.ruleForm = {}
			},
			init_data() {
				this.$axios.get("/health").then(res => {
					console.log(res.data)
				})
			}
		},
		created() {
			this.init_data()

		}
	}
</script>


<style>
	.container {
		margin: 20px 10% 10% 10%;
	}

	.login_form {

		position: absolute;

		top: 10%;
		left: 30%;




	}
</style>



