//axios-init.js 
import axios from 'axios'

let loadingInstance; //Loading 

axios.defaults.baseURL = 'http://modderbug.cn:5004'; // axios
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
// axios.defaults.crossDomain = true;
// axios.defaults.withCredentials = true;  //cross  cookie
axios.defaults.headers.common['Authorization'] = ''; //  Authorization
axios.defaults.timeout = 30000;

// 
axios.interceptors.request.use(
  config => {
    // vuextoken
    // httpheadertokentoken
    // tokentoken
    const token = window.localStorage.getItem("spider_web");
    token && (config.headers.Authorization = token);
    return config;
  },
  error => {
    return Promise.error(error);
  }
);

axios.interceptors.response.use(
  response => {
    // 200
    // 
    if (response.status === 200) {
      return Promise.resolve(response);
    } else {
      return Promise.reject(response);
    }
  },
  // 2
  // 
  // 
  // 
  error => {
    if (error.response.status) {
      switch (error.response.status) {
        // 401: 
        // 
        // 
        case 401:
          vant.Toast.fail("");
          break;

        // 403 token
        // 
        // tokenvuextoken
        // 
        case 403:
          vant.Toast.fail("");
          // token
          break;

        // 404
        case 404:
          vant.Toast.fail("");
          break;
        // 
        default:
          vant.Toast.fail(error.response.data.message);
      }
      return Promise.reject(error.response);
    }
  }
);

export default axios


